@import url(https://fonts.googleapis.com/css?family=Covered+By+Your+Grace);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}





body{
  background-image: url(https://saqy.github.io/meri-rukhmee/static/media/bg-gif.ab77798b.gif) !important;
  background-size: cover !important;

}

@media only screen and (min-width: 572px) {
  body {
    background: black !important;
    }
}



.App {
  text-align: center;
  /* background-image: url('./bg.jpg'); */
  padding: 12px 22px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
  text-align: center;
  font-family: "Covered By Your Grace", cursive;
  color: white;
}

#days {
  font-size: 200px;
  color: #fe828c;
}

#hours {
  font-size: 150px;
  color: #abcd58;
}

#minutes {
  font-size: 100px;
  color: grey;
}

#seconds {
  font-size: 50px;
  color: #f6da74;
}

div {
  display: inline-block;
  line-height: 1;
  font-size: 40px;
}
#timer div {
  padding: 40px 20px;
}

#timer {
    position: relative;
    height: 100vh;
    width: 100vw;
}

#timer .timer-ftr {
    position: fixed;
    bottom: 5px;
    left: 0;
    width: 100%;
    padding: 0;
}
#timer .timer-ftr div {
    padding: 0;
}
span {
  display: block;
  font-size: 20px;
  color: white;
}

.wrap {
  position: relative;
  text-align: center;
  display: flex;
  justify-content: space-between;
  overflow-x: hidden;
  max-width: 100%;
  font-family: "Open Sans", sans-serif;
  margin-top: 50px;
}
.slide {
  position: relative;
  transition: 0.3s all ease;
  margin: 0 5px;
  display: flex;
}
.slideLeft {
  left: 0%;
}
.slideRight {
  right: 0%;
}
.tag-line{
    margin-top: 30px;
    font-size: 12px;
    font-family: 'Courier New', Courier, monospace;
}
#days, #seconds {
    display: block;
}
#hours, #minutes {
    display: inline-block;
    vertical-align: bottom;
    width: 50%;
}

@media only screen and (max-width: 600px) {
    #seconds {
        font-size: 30px;
    }
    #days {
        font-size: 160px;
    }
    #hours {
        font-size: 110px;
    }
    #minutes {
        font-size: 60px;
    }
  }

@media only screen and (max-width: 374px) {
    #timer div {
        padding: 12px 20px;
      }
}
